<template>
  <div class="lsit">
    <Navbar>
      <template v-slot:left>
        <div class="icon"><i class="iconfont icon-jiantou"></i></div>
      </template>
      <template v-slot:default> 订单管理 </template>
      <template v-slot:right> </template>
    </Navbar>
    <van-tabs v-model:active="active" @click="onClick" animated>
      <van-tab title="全部"></van-tab>
      <van-tab title="待支付"></van-tab>
      <van-tab title="已支付"></van-tab>
      <van-tab title="发货"></van-tab>
      <van-tab title="交易完成"></van-tab>
      <van-tab title="过期"></van-tab>
    </van-tabs>
    <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
      <van-list
        v-model:loading="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
        ><div @click="goto(item.id)" v-for="(item, index) in list" :key="index">
          <div class="order-price">
            <div class="price-item">
              <label>订单号：</label>
              <span>{{ item.order_no }}</span>
            </div>
            <div class="price-item">
              <label>创建时间：</label>
              <span>{{ item.created_at }}</span>
            </div>
          </div>
          <van-card
          v-for="(sub, sub_index) in item.orderDetails.data" :key="sub_index"
            :num="sub.num"
            :price="sub.price + '.00'"
            desc="全场包邮"
            :title="sub.goods.title"
            :thumb="sub.goods.cover_url"
          />
        </div>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
import {
  createOrder,
  getOrderPreview,
  payOrder,
  payOrderStatus,
  payOrderList,
  payOrderDetail,
  confirmOrder,
  viewExpress,
} from "network/orders";
import Navbar from "components/common/navbar/Navbar";
import { onMounted, reactive, ref, toRefs } from "vue";
import { useRouter } from 'vue-router';

export default {
  setup() {
    const router = useRouter();

    const state = reactive({
      list: [],
      loading: false,
      finished: false,
      refreshing: false,
      page: 1,
      totalPage: 0,
      status: 0,
    });

    const active = ref(0);

    const onClick = (name) => {
      state.status = name;
      onRefresh();
    }

    const goto = (id) => {
      router.push({path: "/orderdetail",query:{id}})
    }

    onMounted(() => {
      onRefresh();
    });

    const loadData = () => {
      payOrderList({
        page: state.page,
        status: state.status,
        include: "orderDetails.goods",
      }).then((res) => {
        state.list = state.list.concat(res.data);
        state.loading = false;
        state.totalPage = res.meta.pagination.total_pages;

        if (state.page >= state.totalPage) {
          state.finished = true;
        }
        
        // console.log(res);

      });
    };

    const onLoad = () => {

      if(!state.refreshing && state.page < state.totalPage){
        state.page += 1;
      }
      
      if (state.refreshing) {
          state.list = [];
          state.refreshing = false;
        }
      loadData();
    };

    const onRefresh = () => {
      // 清空列表数据
      state.finished = false;

      state.refreshing = true

      // 重新加载数据
      // 将 loading 设置为 true，表示处于加载状态
      state.loading = true;

      state.page = 1;

      onLoad();
    };

    return {
      ...toRefs(state),
      onLoad,
      onRefresh,
      active,
      loadData,
      onClick,
      goto,
    };
  },
  components: {
    Navbar,
  },
};
</script>

<style scoped lang="scss">
.lsit {
  margin-top: 45px;
}
.order-price {
  margin: 0 auto;
  margin-top: 50px;
  width: 90%;
  line-height: 2rem;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  .price-item {
    text-align: left;
    label {
      margin-top: 4px;
      color: #969799;
      font-size: 16px;
    }
    span {
      font-size: 16px;
    }
  }
}
</style>