import { request } from './request';

export function createOrder(params) {
  return request({
    url: '/api/orders',
    method: 'POST',
    params
  })
}

export function getOrderPreview() {
  return request({
    url: '/api/orders/preview',
    method: 'GET'
  })
}

export function payOrder(order, params) {
  return request({
    url: `/api/orders/${order}/pay`,
    params
  })
}

export function payOrderStatus(order) {
  return request({
    url: `/api/orders/${order}/status`,
  })
}

export function payOrderList(params) {
  return request({
    url: '/api/orders',
    params
  })
}

export function payOrderDetail(order) {
  return request({
    url: `/api/orders/${order}`,
    params: {
      include: 'user,orderDetails.goods'
    }
  })
}

export function confirmOrder(order) {
  return request({
    url: `/api/orders/${order}/confirm`,
    method: 'patch'
  })
}

export function viewExpress(order) {
  return request({
    url: `/api/orders/${order}/express`,
  })
}



